<template>
  <!-- 日志管理 -->
  <div class="container">
    <div class="header">
      <el-date-picker
        v-model="value2"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        :picker-options="pickerOptions"
      >
      </el-date-picker>
      <el-button type="danger">批量删除</el-button>
    </div>
    <div class="con">
      <el-table
        :data="tableData"
        style="width: 100%"
        :row-class-name="tableRowClassName"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
          prop="time"
          label="操作时间"
          width="120"
        ></el-table-column>
        <!-- <el-table-column prop="type" label="操作类型" width="80">
          <template slot-scope="scope">
            <el-tag size="mini" type="success" v-if="tableData[scope.$index].type=='新增'">新增</el-tag>
            <el-tag size="mini" type="warning" v-if="tableData[scope.$index].type=='修改'">修改</el-tag>
            <el-tag size="mini" type="danger" v-if="tableData[scope.$index].type=='删除'">删除</el-tag>
          </template>
        </el-table-column> -->
        <el-table-column
          prop="account"
          label="操作账号"
          width="180"
        ></el-table-column>
        <el-table-column
          prop="name"
          label="用户姓名"
          width="180"
        ></el-table-column>
        <el-table-column
          prop="mechanism"
          label="归属机构"
          width="180"
        ></el-table-column>
        <el-table-column
          prop="phone"
          label="手机号码"
          width="120"
        ></el-table-column>
        <el-table-column
          prop="authority"
          label="权限级别"
          width="120"
        ></el-table-column>
        <el-table-column prop="operat" fixed="right" label="操作" width="280">
          <template slot-scope="scope">
            <el-button size="small" type="primary">查看日志详情</el-button>
            <el-button size="mini" type="danger">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="buttonCon">
      <el-pagination background layout="prev, pager, next" :total="1000">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: "user",
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      value1: "",
      value2: "",
      tableData: [
        {
          time: "2016-05-04",
          // type: "删除",
          account: "admin111",
