<style scoped>
.header .el-button {
  margin: 0 10px;
}
.el-input {
  width: 150px;
  margin: 10px 0;
}
.header {
  padding: 10px;
  background-color: #fff;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.buttonCon {
  margin-top: 10px;
  text-align: right;
}

.cell {
